
* {
  margin: 0;
  padding: 0;
}
body,
html {
  width: 100%;
  height: 100%;
  margin: 0;
}
.ce {
  width: 100%;
  position: relative;
}
.ce > img {
  width: 100%;
}
.ceBtn {
  padding: 0.5rem;
  height: 0.7rem;
  width: 4rem;
  position: absolute;
  top: 24rem;
  left: 38%;
  background: #ffe22d;
  z-index: 666;
  border-radius: 24px;
  -webkit-box-shadow: 0px 0px 25px 5px wheat;
          box-shadow: 0px 0px 25px 5px wheat;
  text-align: center;
  line-height: 0.46rem;
  color: black;
}
.ceBtnTwo {
  width: 100%;
  height: 2rem;
  background-image: url(../assets/黄色浮动.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  /*position: absolute;*/
  z-index: 666;
  color: black;
  position: fixed;
  bottom: 0;
  left: 0;
  text-align: center;
  line-height: 0.5rem;
}
.ceBtnThree {
  width: 4rem;
  margin-top: 0.4rem;
  margin-left: 8rem;
  /*margin-right: 0;*/
}
a {
  text-decoration: none;
}



